// -----------------------------------------------------------------------------
// This file contains very basic styles.
// -----------------------------------------------------------------------------

*,
::after,
::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	outline: none;
}

html {
	font-size: 62.5%;
	max-width: 100vw;
	min-width: $min-width;
	overflow: hidden;
}

body {
	color:white;
	color: var(--font-color);
	font-size: 1rem;
	font-size: var(--text);
	font-family: 'Oswald',sans-serif;
	font-family: var(--text-font);
	// background-image: var(--grad-bg-dark);
}

.container {
	padding: 0 var(--space-md);
	max-width: 900px;
	margin: auto;

	@include media($screen-xl) {
		max-width: 1200px;
	}
}

a {
	color: var(--font-color);
	text-decoration: none;
}

li {
	list-style: none;
}

button {
	border: none;
	background: none;
	cursor: pointer;
}

.section {
	overflow: hidden;
}